import { CloudApiGateway } from './gateway';

export type FolderDTO = {
  id: string;
  name: string;
  organizationId: string;
  memberIds: string[];
};

export class FolderAPI {
  async list(organizationId: string): Promise<FolderDTO[]> {
    return CloudApiGateway.get(`organizations/${organizationId}/folders`);
  }

  async create(values: {
    organizationId: string;
    name: string;
    memberIds: string[];
  }): Promise<FolderDTO> {
    return CloudApiGateway.post(`organizations/${values.organizationId}/folders`, {
      name: values.name,
      memberIds: values.memberIds,
    });
  }

  async delete(organizationId: string, folderId: string): Promise<void> {
    return CloudApiGateway.delete(`organizations/${organizationId}/folders/${folderId}`);
  }

  async update(organizationId: string, folder: FolderDTO): Promise<void> {
    return CloudApiGateway.patch(`organizations/${organizationId}/folders/${folder.id}/`, {
      name: folder.name,
      memberIds: folder.memberIds,
    });
  }
}
